import React, { useEffect, useState } from "react";

import AppBar from "@mui/material/AppBar";
import Button from "@mui/material/Button";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import CssBaseline from "@mui/material/CssBaseline";
import Grid from "@mui/material/Grid";
import Stack from "@mui/material/Stack";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import Link from "@mui/material/Link";
import { createTheme, ThemeProvider } from "@mui/material/styles";

import Photo from "entities/Photo";
import Manifest, { getEmptyManifest } from "entities/Manifest";
import RoverSelector from "components/custom/RoverSelector/roverSelector";
import RoversName from "entities/RoversName";
import { getManifest } from "api/missionManifests";

function Copyright() {
  return (
    <Typography variant='body2' color='text.secondary' align='center'>
      {"Copyright © "}
      <Link color='inherit' href='https://mui.com/'>
        Your Website
      </Link>{" "}
      {new Date().getFullYear()}.
    </Typography>
  );
}

const theme = createTheme({
  palette: {
    primary: {
      main: "#aa593e"
    },
    secondary: {
      main: "#693118"
    },
    background: {
      paper: "#fba87e",
      default: "#fedbca"
    },
    action: {
      selectedOpacity: 0.2
    }
  }
});

const Main: React.FC = () => {
  const [roverName, setRoverName] = useState<string>(RoversName[0]);
  const [manifest, setManifest] = useState<Manifest>(getEmptyManifest());

  useEffect(() => {
    getManifest(roverName).then((apiResp) => {
      setManifest(apiResp);
    });
  }, [roverName]);

  const photos: Photo[] = [
    {
      key: "1",
      imgSrc: ""
    },
    {
      key: "2",
      imgSrc: ""
    }
  ];
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AppBar position='relative'>
        <Toolbar>
          <Typography variant='h6' color='inherit' noWrap>
            Mars rovers
          </Typography>
        </Toolbar>
      </AppBar>
      <main>
        {/* Hero unit */}
        <Box
          sx={{
            bgcolor: "background.paper",
            pt: 8,
            pb: 6
          }}
        >
          <Container maxWidth='sm'>
            <Typography
              component='h1'
              variant='h2'
              align='center'
              color='text.primary'
              gutterBottom
            >
              {manifest.name}
            </Typography>
            <Typography variant='h5' align='center' color='text.secondary' paragraph>
              Something short and leading about the collection below—its contents, the creator, etc.
              Make it short and sweet, but not too short so folks don&apos;t simply skip over it
              entirely.
            </Typography>
            <Stack sx={{ pt: 4 }} direction='row' spacing={2} justifyContent='center'>
              <RoverSelector options={RoversName} onSelect={setRoverName} />
            </Stack>
          </Container>
        </Box>
        <Box>
          <Container
            sx={{
              py: 8
            }}
            maxWidth='md'
          >
            {/* End hero unit */}
            <Grid container spacing={4}>
              {photos.map((photo) => (
                <Grid item key={photo.key} xs={12} sm={6} md={4}>
                  <Card
                    sx={{
                      height: "100%",
                      display: "flex",
                      flexDirection: "column"
                    }}
                  >
                    <CardMedia component='img' sx={{}} image={photo.imgSrc} alt={photo.key} />
                    <CardContent sx={{ flexGrow: 1 }}>
                      {photo.heading != null && (
                        <Typography gutterBottom variant='h5' component='h2'>
                          Heading
                        </Typography>
                      )}
                      {photo.description != null && (
                        <Typography>
                          This is a media card. You can use this section to describe the content.
                        </Typography>
                      )}
                    </CardContent>
                    <CardActions>
                      <Button size='small'>View</Button>
                    </CardActions>
                  </Card>
                </Grid>
              ))}
            </Grid>
          </Container>
        </Box>
      </main>
      {/* Footer */}
      <Box sx={{ bgcolor: "background.paper", p: 6 }} component='footer'>
        <Typography variant='h6' align='center' gutterBottom>
          Footer
        </Typography>
        <Typography variant='subtitle1' align='center' color='text.secondary' component='p'>
          Something here to give the footer a purpose!
        </Typography>
        <Copyright />
      </Box>
      {/* End footer */}
    </ThemeProvider>
  );
};

export default Main;
